.header {
    position: relative;
    width: 1920px;
    height: 100px;
    background-color:  #FFFFFF;
  }

  .header__nav {
    margin-left: 180px;
    padding-top: 0;
    display: flex;
}

@media screen and (min-width: 1220px){
.header-menu {
    padding-top: 20px;
 display: flex;
}
}
.header-list {
    padding-left: 410px;
    display: flex;
    justify-content: center;
    align-items: center;
    // padding-top: 52px;
}
.header-list__item {
    margin-right: 40px;
    list-style: none;
   
}
.header-list__link {
    font-family: Work Sans;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #2E2E38;
    text-decoration: none;
}
.header-list__link:hover{
    color: #EF454E;
}
.header-list__item::backdrop{
    content: '';
    position: absolute;
    margin-top: 30px;
    left: 0;
    display: block;
    width: 50px;
    height: 4px;
    background-color: red;
    margin-bottom: -2px;
    border-radius: 2px;
}
@media screen and (min-width: 1200px){
.header__logo {
    margin-right: 38px;
    padding-top: 30px;
    margin-bottom: 0;
}
}
